import React, { Component } from "react";
import { Table, Divider, Tag, Dropdown,Menu } from "antd";
import { Icon } from 'components/util-components/Icon'
import { 
	GlobalOutlined,
	MailOutlined,
	HomeOutlined,
  SafetyOutlined,
  EyeTwoTone,
    EllipsisOutlined, 
    FileAddOutlined,
    FileSearchOutlined,
    CopyTwoTone
} from '@ant-design/icons';
import { Row, Col, Card, Avatar, Button,Badge } from 'antd';
import PageHeaderAlt from 'components/layout-components/PageHeaderAlt'
import AvatarStatus from 'components/shared-components/AvatarStatus';
import Flex from 'components/shared-components/Flex'
import DonutChartWidget from 'components/shared-components/DonutChartWidget'
import { 
  regionData, 
  sessionData, 
  sessionLabels, 
  conbinedSessionData,
  sessionColor,
  pagesViewData,
  socialMediaReferralData,
  uniqueVisitorsDataWeek,
  uniqueVisitorsDataDay,
  uniqueVisitorsDataMonth
} from './AnalyticDashboardData'
import BasicRight from './drawer-right.js';
import BasicRightConfig from './drawer-right-config.js';


const ProfileInfo = props => (
	<Card>
		<Row justify="center"> 
			<Col sm={24} md={23}>
				<div className="d-md-flex">
					<div className="rounded p-2 bg-white shadow-sm mx-auto" style={{'marginTop': '-3.5rem', 'maxWidth': `${props.avatarSize + 16}px`}}>
						<Avatar shape="square" size={props.avatarSize} src="/img/avatars/thumb-15.jpg" />
					</div>
					<div className="ml-md-4 w-100">
						<Flex alignItems="center" mobileFlex={false} className="mb-3 text-md-left text-center">
							<h2 className="mb-0">Walmart Canada</h2>
							
						</Flex>
						<Row gutter="16"> 
							<Col sm={24} md={8}>
								<p className="mt-0 mr-3 text-muted text-md-left text-center">
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do. 
								</p>
							</Col>
              <Col xs={24} sm={24} md={12}>
								<Row className="mb-2"> 
									<Col xs={12} sm={12} md={9}>
										<Icon type={MailOutlined} className="text-primary font-size-md"/>
										<span className="text-muted ml-2">System admin email:</span>
									</Col>
									<Col xs={12} sm={12} md={15}>
										<span className="font-weight-semibold">bill.hecill@walmart.com</span>
									</Col>
								</Row>
								<Row> 
									<Col xs={12} sm={12} md={9}>
										<Icon type={SafetyOutlined} className="text-primary font-size-md"/>
										<span className="text-muted ml-2">License:</span>
									</Col>
									<Col xs={12} sm={12} md={15}>
                    <Tag color="green" >
                      Sept 22, 2020
                    </Tag>
									</Col>
								</Row>
							</Col>              
							
							
						</Row>
					</div>
				</div>
			</Col>
		</Row>
	</Card>
)


const columns = [
  {
    title: "Profile Name",
    dataIndex: "name",
    key: "name",
    render: text => <a href="/#">{text}</a>
  },
  {
    title: "Description",
    dataIndex: "description",
    key: "description"
  },
  {
    title: "Started",
    dataIndex: "date_started",
    key: "date_started"
  },
  {
    title: "Ended",
    dataIndex: "date_ended",
    key: "date_ended"
  },
  {
    title: "Status",
    key: "tags",
    dataIndex: "tags",
    render: tags => (
      <span>
        {tags.map(tag => {
          let color = "geekblue";
          if (tag === "Success") {
            color = "green";
          }
          
          if (tag === "Failed") {
            color = "volcano";
          }
          return (
            <Tag color={color} key={tag}>
              {tag.toUpperCase()}
            </Tag>
          );
        })}
      </span>
    )
  },
  {
    title: "Summary",
    dataIndex: "deployment_summary",
    key: "deployment_summary"
  }
];

const data = [
  {
    key: "1",
    name: "Patch 12345",
    description: "Deploying an app patch to the Inventory application",
    date_started: "9/1/2020 8:00",
    date_ended: "",
    deployment_summary: "",
    tags: ["Processing"]
  },
  {
    key: "2",
    name: "Device lockdown",
    description: "Deploying configuration updates",
    date_started: "8/27/2020 10:15",
    date_ended: "8/27/2020 10:25",
    deployment_summary: "100 / 100 devices updated",
    tags: ["Success"]
  },
  {
    key: "3",
    name: "App use case",
    description: "Deploying an update to change the configuration",
    date_started: "8/1/2020 8:00",
    date_ended: "8/1/2020 8:05",
    deployment_summary: "0 / 100 devices updated",
    tags: ["Failed"]
  }
];

const crudOptions = (
    <Menu>
      <Menu.Item key="0">
        <span>
          <div className="d-flex align-items-center">
            <EyeTwoTone />
            <span className="ml-2">View</span>
          </div>
        </span>
      </Menu.Item>
      <Menu.Item key="1">
        <span>
          <div className="d-flex align-items-center">
            <CopyTwoTone />
            <span className="ml-2">Clone</span>
          </div>
        </span>
      </Menu.Item>
    </Menu>
  );
  const enrollmentButton = () => (
    <BasicRight />
  )
  const configButton = () => (
    <BasicRightConfig />
  )
  const columns_config = [
    {
      title: "Profile Name",
      dataIndex: "name",
      key: "name",
      render: text => <a href="/#">{text}</a>
    },
    {
      title: "Description",
      dataIndex: "description",
      key: "description"
    }
  ];
  const data_config = [
    {
      key: "1",
      name: "Security Profile",
      description: "This profile applies security settings to devices",
      date_created: "Sep 1, 2020",
      tags: ["OEMConfig"]
    },
    {
      key: "2",
      name: "Inventory App configuration",
      description: "This profile configures Enterprise browser for the Inventory App",
      date_created: "Sep 10, 2020",
      tags: ["Enterprise Browser"]
    },
    {
      key: "3",
      name: "Touch Mode - Delivery",
      description: "This profile configures the device for gloved touch mode users",
      date_created: "Sep 22, 2020",
      tags: ["OEMConfig"]
    }    
  ];
export class Basic extends Component {
  render() {
    const avatarSize = 150;
    return (
      <>
      <PageHeaderAlt background="/img/others/img-12.jpg" cssClass="bg-primary" overlap>
        <div className="container text-center">
          <div className="py-5 my-md-5">
          </div>
        </div>
      </PageHeaderAlt>
      <ProfileInfo avatarSize={avatarSize} />

      <Row gutter={16}>
        
        <Col xs={24} sm={24} md={8}>
          <DonutChartWidget 
            series={sessionData} 
            labels={sessionLabels} 
            title="Devices"
            customOptions={{colors: sessionColor}}
            extra={
              <Row  justify="center">
                <Col xs={20} sm={20} md={20} lg={24}>
                  <div className="mt-4 mx-auto" style={{maxWidth: 200}}>
                    {conbinedSessionData.map(elm => (
                      <Flex alignItems="center" justifyContent="between" className="mb-3" key={elm.label}>
                        <div>
                          <Badge color={elm.color} />
                          <span className="text-gray-light">{elm.label}</span>
                        </div>
                        <span className="font-weight-bold text-dark">{elm.data}</span>
                      </Flex>
                    ))}
                  </div>
                </Col>
              </Row>
            }
          />
                              <Card title="Production testing devices" >
                    <h6>This enrollment profile is for production devices for testing prior to full rollout</h6>

                    <img className="img-fluid" src="/img/others/barcode.jpg" alt="" />
										</Card>
        </Col>
        <Col xs={24} sm={24} md={16}>

                    <Card title="Latest Configuration Profiles"  extra={configButton()}>
                        <Table 
                          className="no-border-last" 
                          columns={columns_config} 
                          dataSource={data_config} 
                          rowKey='id' 
                          pagination={false}
                        />

                      </Card>
								

          <Card title="Latest Deployments" extra={enrollmentButton()}>
              <Table 
                className="no-border-last" 
                columns={columns} 
                dataSource={data} 
                rowKey='id' 
                pagination={false}
              />
            </Card>        
        </Col>
      </Row>

      </>
    )

    ;
  }
}

export default Basic;
